import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class About extends Component {
  render() {
    return (
      <div>
        <div>
          <br />
          <br />
          <Link to="/"></Link><br />
          <br />
          <Link to="/news"></Link><br />
          <br />
          <Link to="/counter"></Link>
        </div>
      </div>
    )
  }
}

export default About



