import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class Home extends Component {
  render() {
    return (
      <div>
        <br />
        <br />
        <Link to="/news"></Link><br />
        <br />
        <Link to="/about"></Link><br />
        <br />
        <Link to="/counter"></Link>
      </div>
    )
  }
}



